/**
 * better-scroll vue
 */

<template>
  <div ref="scroll" :class="c()">
    <div :class="c('__wrapper')">
      <div ref="scrollContent">
        <slot v-if="updateData[0].length != 0" />
        <div style="text-align: center;margin-top: 50%;" v-if="updateData[0].length==0">
        	<img :src="require('./empty.png')" />
        </div>
      </div>

      <slot :pullUp="pullUp" :pullUpNow="pullUpNow" name="pullup">
        <div v-if="pullUp" :class="c('__pullup')">
          <div v-if="!pullUpNow">
            <span>{{ pullUpTxt }}</span>
          </div>
          <div v-else>
            <loading></loading>
          </div>
        </div>
      </slot>
    </div>

    <slot
      :pullDown="pullDown"
      :pullDownStyle="pullDownStyle"
      :pullDownBefore="pullDownBefore"
      :pullDownNow="pullDownNow"
      :bubbleY="bubbleY"
      name="pulldown">
      <div v-if="pullDown" ref="pulldown" :style="pullDownStyle" :class="c('__pulldown')">
        <div v-if="pullDownBefore" :class="c('__pulldown__before')">
          <Bubble :y="bubbleY" />
        </div>
        <div v-else :class="c('__pulldown__after')">
          <div v-if="pullDownNow">
            <loading></loading>
          </div>
          <div v-else><span>{{ pullDownTxt }}</span></div>
        </div>
      </div>
    </slot>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import loading from './Loading'
import Bubble from './Bubble'
import mixin from './mixins'
import { timeout } from './utils'

export default {
  name: 'meRefreshLoad',
  components: {
    loading,
    Bubble,
  },
  mixins: [mixin],
  props: {
  	emptyPic: {
  		type: String,
  		default: './empty.png'
  	},
    probeType: {
      // 
      type: Number,
      default: 1,
    },
    click: {
      // 
      type: Boolean,
      default: true,
    },
    listenScroll: {
      // 
      type: Boolean,
      default: false,
    },
    listenBeforeScrollStart: {
      // 
      type: Boolean,
      default: false,
    },
    scrollX: {
      // X
      type: Boolean,
      default: false,
    },
    scrollY: {
      // Y
      type: Boolean,
      default: true,
    },
    scrollbar: {
      // 
      type: null,
      default: false,
    },
    pullDown: {
      // 
      type: Boolean,
      default: true,
    },
    pullDownConfig: {
      // 
      type: Object,
      default: () => ({
        threshold: 90, //  pullingDown 
        stop: 40, // pullingDown  hold 
        txt: '',
      }),
    },
    pullUp: {
      // 
      type: Boolean,
      default: true,
    },
    pullUpConfig: {
      // 
      type: Object,
      default: () => ({
        threshold: 100, //  pullingUp 
        txt: { more: '', noMore: '  ', noData: '~' },
      }),
    },
    startY: {
      // Y
      type: Number,
      default: 0,
    },
    bounce: {
      // 
      type: Boolean,
      default: true,
    },
    bounceTime: {
      // 
      type: Number,
      default: 600,
    },
    preventDefaultException: {
      // 
      type: Object,
      default: () => ({
        tagName: /^(INPUT|TEXTAREA|BUTTON|SELECT)$/,
      }),
    },
    mouseWheel: {
      //  PC 
      type: Boolean,
      default: false,
    },
    betterScrollOptions: {
      //  better-scroll  better-scroll  props  props 
      type: Object,
      default: null,
    },
    autoUpdate: {
      // :updateData/refreshData
      type: Boolean,
      default: false,
    },
    updateData: {
      // //
      type: Array,
      default: null,
    },
    refreshData: {
      //  updateData 
      type: Array,
      default: null,
    },
  },
  data () {
    return {
      pullDownBefore: true, // 
      pullDownNow: false, // 
      pullDownStyle: '', // 
      pullUpNow: false, // 
      pullUpFinally: false, // true
      isRebounding: false, // 
      bubbleY: 0, // y
    }
  },
  computed: {
    // 
    pullDownTxt () {
      return this.pullDownConfig && this.pullDownConfig.txt
    },
    // 
    pullUpTxt () {
      const moreTxt = this.pullUpConfig && this.pullUpConfig.txt && this.pullUpConfig.txt.more
      const noMoreTxt = this.pullUpConfig && this.pullUpConfig.txt && this.pullUpConfig.txt.noMore
      const noDataTxt = this.pullUpConfig && this.pullUpConfig.txt && this.pullUpConfig.txt.noData
			return this.pullUpFinally ? (this.updateData[0].length>0 ? noMoreTxt : noDataTxt) : moreTxt
    },
  },
  watch: {
    updateData () {
      this.update()
    },
    async refreshData () {
      if (this.pullDownUpdateState || this.pullUpUpdateState) return

      await this.$nextTick()
      this.refresh()
    },
  },
  activated () {
    this.refresh()
  },
  async mounted () {
  	console.log(this.updateData)
    this.pullDownInitTop = parseInt(this.$refs.pulldown && getComputedStyle(this.$refs.pulldown).top) || -50

    await this.$nextTick()
    this.initScroll()

    //  $data
    this.autoUpdate && this.$parent && this.$parent.$data && this.$watch(() => this.$parent.$data, (val) => {
      this.update()
    }, {
      deep: true,
    })
  },
  methods: {
    // scroll
    initScroll () {
      if (!this.$refs.scroll) return

      // scrollContent
      if (this.$refs.scrollContent) {
//      this.$refs.scrollContent.style.minHeight = `${this.$refs.scroll.getBoundingClientRect().height + 1}px`
      }

      const options = {
        ...this.betterScrollOptions,
        probeType: this.probeType,
        click: this.click,
        scrollX: this.scrollX,
        scrollY: this.scrollY,
        scrollbar: this.scrollbar,
        pullDownRefresh: this.pullDown && this.pullDownConfig,
        pullUpLoad: this.pullUp && this.pullUpConfig,
        startY: this.startY,
        bounce: this.bounce,
        bounceTime: this.bounceTime,
        preventDefaultException: this.preventDefaultException,
        mouseWheel: this.mouseWheel,
      }

      this.scroll = new BScroll(this.$refs.scroll, options)

      this.listenScroll &&
        this.scroll.on('scroll', pos => {
          this.$emit('scroll', pos)
        })

      this.listenBeforeScrollStart &&
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScrollStart')
        })

      this.pullDown && this._initPullDown()

      this.pullUp && this._initPullUp()
    },
    // 
    _initPullDown () {
      this.scroll.on('pullingDown', () => {
        this.pullDownBefore = false
        this.pullDownNow = true
        this.$emit('pullingDown')
        this.scroll.closePullDown() //  bounce 
      })

      this.scroll.on('scroll', pos => {
        if (!this.pullDown || pos.y < 0) return

        const posY = Math.floor(pos.y) // yNumber

        if (this.pullDownBefore) {
          this.bubbleY = Math.max(0, posY + this.pullDownInitTop)
          this.pullDownStyle = `transform: translateY(${Math.min(posY, -this.pullDownInitTop)}px)`
        } else {
          this.bubbleY = 0
        }

        if (this.isRebounding) {
          this.pullDownStyle = `transform: translateY(${Math.min(posY, this.pullDownConfig.stop)}px)`
        }
      })
    },
    // 
    _initPullUp () {
      this.scroll.on('pullingUp', () => {
        if (this.pullUpFinally) {
          this.scroll.finishPullUp()
        } else {
          this.pullUpNow = true
          this.$emit('pullingUp')
        }
      })
    },
    // 
    disable () {
      this.scroll && this.scroll.disable()
    },
    // 
    enable () {
      this.scroll && this.scroll.enable()
    },
    // 
    destroy () {
      this.scroll && this.scroll.destroy()
    },
    // 
    refresh () {
      this.scroll && this.scroll.refresh()
    },
    // /
    async update (final) {
      if (this.pullDown && this.pullDownNow) {
        if (this.pullDownUpdateState) return
        this.pullDownUpdateState = true // update

        // 
        this.pullDownNow = false
        await timeout(this.bounceTime / 2) // hold
        this.isRebounding = true
        this.scroll.finishPullDown() // 
        await timeout(this.bounceTime)
        this.pullDownBefore = true
        this.isRebounding = false
        this.scroll.openPullDown(this.pullDownConfig)

        this.pullUpFinally = false
      } else if (this.pullUp && this.pullUpNow) {
        if (this.pullUpUpdateState) return
        this.pullUpUpdateState = true // update

        // 
        this.pullUpNow = false
        this.scroll.finishPullUp()
      }

      typeof final !== 'undefined' && (this.pullUpFinally = !!final)

      await this.$nextTick()
      this.refresh()

      this.pullDownUpdateState = false
      this.pullUpUpdateState = false
    },
    /**
     * 
     * @param  {Number} x    x
     * @param  {Number} y    y
     * @param  {Number} time 
     * @return {Void}
     */
    scrollTo (x = 0, y = 0, time = this.bounceTime) {
      this.scroll && this.scroll.scrollTo(x, y, time)
    },
    // 
    scrollToElement () {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    },
    // 
    scrollToTop () {
      this.scroll && this.scrollTo(0, 0)
    },
    // 
    scrollToBottom () {
      this.scroll && this.scrollTo(0, this.scroll.maxScrollY)
    },
    // 
    autoPullDownRefresh () {
      this.scroll && this.scroll.autoPullDownRefresh()
    },
  },
}
</script>

<style lang="stylus">
$ = vue-slim-better-scroll;
.{$} {
  width: 100%;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  position: relative;

  &__wrapper {
    position: relative;
    z-index: 1;
  }

  &__pullup {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: rgb(153, 153, 153);
  }

  &__pulldown {
    position: absolute;
    left: 0;
    top: -50px; /* no */
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all;
    font-size: 14px;
    color: rgb(153, 153, 153);

    &__before {
      display: flex;
    }

    &__after {
      width: 100%;
      height: 40px; /* no */
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
</style>
