<template>
	<div>
		<div class="me_input">
			<div class="me_input_left">
				<span v-show="isMust" style="color: red;">*</span>
				<span></span>
			</div>
			<div class="me_input_right">
				<input :type="type"
					:placeholder="placeholder"
					:disabled="disabled"
					:readonly="readonly"
					:maxlength="maxlength"
					:showClose="showClose"
					v-model="inputValue"
					:style="{textAlign: align}"
					@input="meInput"
					@focus="meFocus"
					@blur="meBlur"
				/>
				<me-icon name="close" class="clearBtn" v-if="inputValue!='' && showClose"
					@click="clearValue()"></me-icon>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'meInput',

	data () {
		return {
			inputValue: ''
		}
	},

	props: {
		type: { // 
			type: String,
			default: 'text'
		},
		placeholder: {
			type: String,
			default: ''
		},
		disabled: { // 
			type: Boolean,
			default: false
		},
		readonly: { // 
			type: Boolean,
			default: false
		},
		isMust: { // *
			type: Boolean,
			default: false
		},
		align: { // 
			type: String,
			default: 'left'
		},
		maxlength: { // 
			type: String,
			default: ''
		},
		showClose: { // 
			type: Boolean,
			default: false
		},
		value: {}
	},

	watch: {
    inputValue (val) {
      this.$emit('input', val)
    },
    value (val) {
      this.inputValue = val
    }
  },

  mounted () {
  	this.inputValue = this.value
  },

	methods: {
		meFocus (el) { // el
			this.$emit('meFocus', el)
		},

		meInput () { // 
			this.$emit('meChange', this.inputValue)
		},

		meBlur (el) { // el
			this.$emit('meBlur', el)
		},

		clearValue () { // 
			this.inputValue = ''
		}
	}
}
</script>

<style scoped="scoped" lang="less">
@import url("../../assets/css/index.less");
	.me_input{
		padding: 15px 8px;
		display: flex;
		align-items: center;
		background-color: @me-bg-normal;
		border-bottom: 1px solid #DDDDDD;
		.me_input_left{
			display: flex;
			align-items: center;
			min-width: 30%;
			text-align: left;
			padding-right: 5px;
		}
		.me_input_right{
			flex: 1;
			text-align: left;
			position: relative;
			input{
				width: 100%;
				padding-right: 20px;
				box-sizing: border-box; /**/
				-webkit-box-sizing: border-box; /**/
				-moz-box-sizing: border-box; /**/
				outline: none;
				border: none;
				background-color: @me-bg-normal;
			}
			.clearBtn{
				position: absolute;
				z-index: 2;
				right: 0;
				top: 50%;
				transform:translateY(-50%);
				color: @font-color-lighter;
				font-size: @font-size-lg;
			}
		}
	}
</style>