import config from '../config'
let baseUrl
switch (process.env.NODE_ENV) {
  // 
  case 'production':
    baseUrl = config.baseUrl.pro
    break
  // 
  case 'testing':
    baseUrl = config.baseUrl.tes
    break
  default:
    // 
    baseUrl = config.baseUrl.dev
    break
}
export { baseUrl }
export default {
  // ****
  top_list: baseUrl + '/top/list',
  mockTest: baseUrl + '/api/mockTest'
}
