<template>
	<div class="lottery-box" id="app">
	  <div class="lottery">
	    <div class="lottery-item">
	      <div class="lottery-start">
	        <div :class="[{active: lotteryTime<=0}, 'box']" @click="startLottery">
	                  
	        </div>
	      </div>
	      <ul>
	      	<slot>
		        <li v-for="(item,i) in list" :class="i==index?'on':''">
		          <div class="box">
		            {{item.title}}
		          </div>
		        </li>
	      	</slot>
	      </ul>
	    </div>
	  </div>
	</div>
</template>

<script>
export default {
	name: 'meLottery',
	data () {
		return {
			index: -1, // 
			count: 8, // 
			timer: 0, // 
			speed: 200, // 
			times: 0, // 
			cycle: 50, // 
			click: true, // 
		}
	},
	props: {
		list: { // 
			type: Array,
			default: () => []
		},
		prize: { // 
			type: Number,
			default: 0
		},
		lotteryTime: { // 
			type: Number,
			default: 0
		}
	},
	methods: {
		startLottery() { // 
			if (!this.click || this.lotteryTime <= 0) { return }
			this.click = false
			this.startRoll()
		},

		startRoll() { // 
			this.times += 1 // 
			this.oneRoll() //  ,1
			//  && 
			if (this.times > this.cycle + 10 && this.prize === this.index) { // 
				this.winPrice() // 
			} else {
				if (this.times < this.cycle) { // [0-50)
					this.speed -= 10 // 
				} else if (this.times > this.cycle + 10 && ((this.prize === 0 && this.index === 7) || this.prize === this.index + 1)) { // 
					this.speed += 110 // 601 iftrue().
				} else {  // [51,60],10
					this.speed += 20 // 20,1.
				}
				if (this.speed < 40) { this.speed = 40 }
				this.timer = setTimeout(this.startRoll, this.speed)
			}
		},

		oneRoll () { // 
			let index = this.index // 
			const count = this.count // 
			index += 1
			if (index > count - 1) { index = 0 } // 012~7 80
			this.index = index // 1,1;
		},

		winPrice() { // 
			clearTimeout(this.timer)  //     ,else,.
			this.times = 0
			this.speed = 200
			this.click = true
			this.$emit('success')
		}
	}
}
</script>

<style scoped="scoped">
img {
  border: 0px;
}
ul, li {
  list-style-type: none;
}
.lottery-box {
  overflow: hidden;
}
.lottery {
  animation: changeBg .5s ease infinite;
  overflow: hidden;
  height: 375px;
  width: 100%;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@keyframes changeBg {
0% {
background-image:url(./img/k1.png);
}
100% {
background-image:url(./img/k2.png);
}
}
.lottery .lottery-item {
    height: 85%;
    width: 85%;
    position: relative;
}
.lottery .lottery-item ul{
	height: 100%;
}
.lottery .lottery-item ul li {
    width: 30%;
    height: 30%;
    position: absolute;
}
.lottery .lottery-item ul li:nth-child(2) {
    left: 35%;
}
.lottery .lottery-item ul li:nth-child(3) {
    left: 70%;
}
.lottery .lottery-item ul li:nth-child(4) {
    left: 70%;
    top: 35%;
}
.lottery .lottery-item ul li:nth-child(5) {
    left: 70%;
    top: 70%;
}
.lottery .lottery-item ul li:nth-child(6) {
    left: 35%;
    top: 70%;
}
.lottery .lottery-item ul li:nth-child(7) {
    left: 0;
    top: 70%;
}
.lottery .lottery-item ul li:nth-child(8) {
    left: 0;
    top: 35%;
}
.lottery .lottery-item ul li .box, .lottery .lottery-item .lottery-start .box {
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color: white;
    box-shadow: 0px 3px 5px 3px rgba(0, 0, 0, .4);
    border-radius: 5px;
}
.lottery .lottery-item ul li .box{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.lottery .lottery-item ul li.on .box {
    background-color: #DF3031;
}
.lottery .lottery-item ul li.on .box p {
    color: #fff;
}
.lottery .lottery-item .lottery-start {
    position: absolute;
    width: 30%;
    height: 30%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.lottery .lottery-item .lottery-start .box {
    cursor: pointer;
    overflow: hidden;
    background-color: #DF3031;
}
.lottery .lottery-item .lottery-start .active {
    opacity: 0.5;
}
</style>