<template>
	<div style="height: 100%;">
		<mescroll-vue ref="mescroll" :up="mescrollUp" style="height: 100%;">
			<div id="dataList" class="data-list" v-cloak>
				<div class="item_msg" v-for="(xx, index) in dataList" :key="index">
					{{xx}}
				</div>
			</div>
		</mescroll-vue>
	</div>
</template>

<script>
import MescrollVue from 'mescroll.js/mescroll.vue'
export default {
	name: 'meRefreshLoadMescroll',
	components: {
    MescrollVue // mescroll
  },
  data () {
  	return {
  		mescrollDown: {},
  		mescrollUp: { // 
  			callback: this.upCallback, // page, mescroll
  			page: {
  				num: 0,
  				size: 10
  			},
  			empty: {
          warpId: 'dataList',
          icon: require('./empty.png'),
          tip: "~",
//        btntext: ' >', // ,""
//        btnClick () { // ,null
//          alert(',')
//        }
        },
//      toTop: { // 
//      	src: require('./totop.png'),
//      	offset: 300 // 300px
//      }
  		},
  		dataList: [] // 
  	}
  },
  methods: {
  	upCallback (page, mescroll) {
			this.getData(page).then((res) => {
				// 
        if (page.num === 1) this.dataList = []
				// 
				this.dataList.push(...res)
				// ,
				this.$nextTick(() => {
          mescroll.endSuccess(res.length)
        })
			}).catch(() => {
				// ,
				mescroll.endErr()
			})
  	},

  	async getData (page) {
  		return new Promise(async (resolve, reject) => {
  			await setTimeout(() => {
//				resolve([])
					if (page.num == 1) {
						resolve(Array.from({ length: page.size }, (value, index) => `${page.num}${index},()`))
					} else {
						resolve([])
					}
  			}, 2000)
  		})
  	}
  },
  beforeRouteEnter (to, from, next) { // isBounce,beforeRouteEnter
    next(vm => {
      // ,isBounce
      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter()
    })
  },
  beforeRouteLeave (to, from, next) { // isBounce,beforeRouteLeave
    // ,isBounce
    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave()
    next()
  }
}
</script>

